.formContainer {
  max-width: 1200px;
  width:75%;
  margin: 0.5rem auto; /* 上下 0.5rem，左右自动（居中） */
  padding: 1.5rem;
  background: #ffffff;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
}

/* 统一表单行样式 */
.formRow {
  margin-bottom: 1.5rem;
  padding: 0.5rem;
}



/* 年月日行样式 */
.formRow1 {
  display: flex;
  justify-content: center;
  gap: 0.8rem;
  margin: 1.5rem 0;
}
.formRow3 {
  display: flex;
  margin-bottom: 2rem;
  padding: 1rem;
  margin-bottom: 0rem;
}
/* 姓名输入样式 */
.inputGroup {
  position: relative;
  width: 90%;
  margin: 0 auto;
}

.underlineInput {
  width: 100%;
  padding: 0.8rem 0;
  border: none;
  border-bottom: 2px solid #007bff;
  font-size: 1.1rem;
  background: transparent;
  transition: border-color 0.3s ease;
}

.underlineInput:focus {
  outline: none;
  border-color: #0056b3;
}

.optional {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  color: #6c757d;
  font-size: 0.9rem;
}

/* 其他保持原有样式优化 */
.buttonGroup {
  display: flex;
  gap: 2rem;
  justify-content: center;
  margin: 1rem 0;
}

.choiceButton {
  padding: 0.8rem 1.6rem;
  border: 2px solid #007bff;
  border-radius: 25px;
  background: white;
  color: #007bff;
  cursor: pointer;
  transition: all 0.3s ease;
}

.choiceButton.active {
  background: #007bff;
  color: white;
}

/* 日期选择容器 */
.dateSelectGroup {
  display: flex;
  gap: 0.5rem;
  width: 100%;
  padding: 0.5rem 0;
  overflow-x: auto; /* 允许横向滚动 */
  scrollbar-width: none; /* 隐藏滚动条 */
  -ms-overflow-style: none;
}

.dateSelectGroup::-webkit-scrollbar {
  display: none;
}

/* 日期选择器基础样式 */
.dateSelect {
  flex: 0 0 calc(33% - 0.5rem); /* 基础宽度 */
  min-width: 90px;
  padding: 0.8rem 1rem;
  border: 2px solid #007bff;
  border-radius: 8px;
  background: white;
  font-size: 0.9rem;
  cursor: pointer;
  white-space: nowrap; /* 防止文字换行 */
}

/* 时间选择器适配 */
.timeSelectGroup {
  display: flex;
  gap: 0.5rem;
  justify-content: center;
  padding: 0.5rem 0;
}

/* 通用选择器样式优化 */
select {
  appearance: none;
  background-image: url("data:image/svg+xml...");
  background-repeat: no-repeat;
  background-position: right 0.8rem center;
  background-size: 12px;
}

/* 通用下划线选择样式 */
.underlineSelect {
  width: 200px;
  padding: 0.8rem 0;
  border: none;
  border-bottom: 2px solid #007bff;
  background: transparent;
  appearance: none;
  font-size: 1rem;
  color: #495057;
  cursor: pointer;
}

.underlineSelect:focus {
  outline: none;
  border-color: #0056b3;
}




/* 城市选择样式 */
.citySelect {
  width: 80%;
  display: block;
  margin: 0 auto;
  padding: 0.8rem;
  border: 2px solid #007bff;
  border-radius: 8px;
  background: white;
  color: #495057;
  cursor: pointer;
}

/* 提交按钮样式 */
.submitButton {
  display: block;
  width: 80%;
  margin: 1rem auto;
  padding: 1.2rem;
  background: #dc3545;
  color: white;
  border: none;
  border-radius: 30px;
  font-size: 1.2rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.submitButton:hover {
  background: #c82333;
  transform: translateY(-2px);
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.15);
}

/* 响应式调整 */
@media (max-width: 480px) {
  .formRow1 {
    flex-wrap: wrap;
  }
  
  .dateSelectGroup {
    flex-wrap: wrap;
  }
  
  .dateSelect {
    flex: 1 1 30%;
    min-width: auto;
  }
}